import React from 'react';
import ReactDOM from 'react-dom';
import { Route, HashRouter } from 'react-router-dom';
import './assets/css/common.css';
import './assets/css/icon.css'
// import Login from './pages/user/login'
import ForgetPwd from './pages/user/forgetPassword'
import ResetPassword from './pages/user/resetPassword'
function Login () {
  return new Promise((resolve, reject) => {
    import('./pages/user/login').then((component) => {
      resolve(component)
    })
  })
}
function ReplaceContent () {
  return (
    <div>这里是替代显示页面</div>
  )
}

ReactDOM.render(
  <React.Suspense fallback={<ReplaceContent/>}>
    <HashRouter>
      <Route path='/' exact component={React.lazy(Login)} />
      <Route path='/forgetPwd' exact component={ForgetPwd}/>
      <Route path='/resetPassword' component={ResetPassword}/>
    </HashRouter>
  </React.Suspense>
  ,
  document.getElementById('root')
);
